import React from 'react';
import './label.css';

interface InputProps {
    /**
     * Input 텍스트
     */
    text: string;

    /**
     * Input placeHolder 텍스트
     */
    placeHolder: string;

    /**
     * style 정의
     */
    style?: object;
}

export const Input = ({
    text,
    style,
    placeHolder,
    ...props
}) => {
    
    return <input style={style} placeHolder={placeHolder}>
        { text }
    </input>;
};